const timeFormat = (ms /**number*/) => {

    //   ,  ,  
    let sign = ''
    if (ms < 0) {
        sign = '-'
        ms = ms * -1
    }

    const num = (val) => {
        val = Math.floor(val)

        return 1 < val && val < 10 ? '0' + val : val
    }

    const sec     = ms  / 1000
    const day     = sec / (3600 * 24)
    const hours   = sec / 3600  % 24
    const minutes = sec / 60 % 60
    const seconds = sec % 60

    const str = Math.floor(day) ? `${Math.floor(day)} . ` : ''

    //      
    if (day < 1 && hours < 1 && minutes < 3) {
        return `${sign} ${ num(minutes) } ${ num(seconds) }`.trim();
    }

    return `${sign} ${ str }${ num(hours) } ${ num(minutes) }`.trim();
}

export default timeFormat