            1.1.     (    ) : {{ products_name_and_cost }}   ,    {{ products_cost }} .<br>
            1.2.       : {{ order.start_time }}<br>
            1.3.  : {{ deposit }}<br>
            1.4.              .             .<br>
        </p>                

        <h3 class="print__caption">2.     </h3>
        <p class="print__p">
            2.1.     :<br>
